export default {
	data() {
		return {
			skim: {
				period: '',
				imgUrl: '',
				name: '',
				joinCount: '',
				closeDate: '',
			},
			personlList: [
				// '../../../static/img/release.png',
				// '../../../static/img/release.png',
				// '../../../static/img/release.png',
				// '../../../static/img/release.png',
				// '../../../static/img/release.png',
				// '../../../static/img/release.png',
				// '../../../static/img/release.png',
				// '../../../static/img/release.png',
				// '../../../static/img/release.png'
			],
			show: false,
			credit: 0,
			loading: false,
			nocredit: false
		}
	},
	onLoad(options) {
		this.skim = JSON.parse(options.data)
		this.credit = this.$store.state.task.credit
		this.getMember()
	},
	computed: {
		remainTimes() {
			return Math.floor(this.credit/50)
		}
	},
	methods: {
		cancel() {
			this.show = false
		},
		joinBtn(period) {
			if (this.loading) return
			this.loading = true
			if (this.remainTimes > 0) {
				this.show = true
				this.nocredit = false
				this.updateCredit()
			} else {
				uni.showToast({
					title: '积分不足',
					icon: 'none'
				});
				this.show = true
				this.nocredit = true
				this.loading = false
			}
		},
		getAll(period) {
			uni.navigateTo({
				url: `./draw-partic?period=${this.skim.period}&count=${this.skim.joinCount}`
			})
		},
		goTabbar() {
			uni.switchTab({
			    url: '../tabbar-3/tabbar-3'
			});
		},
		updateCredit() {
			this.doRequest('/order/create', {
				period: this.skim.period,
				name: this.skim.name,
				uuid: this.$store.state.uuid,
				nickName: this.$store.state.userInfo.nickName,
				avatarUrl: this.$store.state.userInfo.avatarUrl
			}).then(res=>{
				uni.showToast({
					title: '参与成功，等待开奖',
					icon: 'none'
				})
				this.credit -= 50
				this.skim.joinCount += 1
				this.$store.commit('updateTask', {
					credit: -50,
					totalDrawTimes: 1
				})
				this.loading = false
			}).catch(_=>{
				this.loading = false
			})
		},
		getMember() {
			this.doRequest('/order/get7', {
				period: this.skim.period
			}).then(res=>{
				this.personlList = res.data
			})
			
		}
	},
	onShareAppMessage(res) {
		uni.showToast({
			title: 'share'
		})
	    return {
	      title: '王者荣耀，免费抽皮肤，和好兄弟开黑...',
	      path: '/pages/tabbar/login/login?parentID='+ this.$store.state.uuid
	    }
	}
}
